import { Module } from 'vuex';
import { ExamInfoState, RootState } from '@/store/types';
import axios from 'axios';

const examInfo: Module<ExamInfoState, RootState> = {
  namespaced: true,
  state: {

  },
  mutations: {

  },
  actions: {
    getExamInfoList({ commit, rootState }, params) {
      const temp = {
        ...params,
        UserGuid: rootState.userId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/ExamInfo/GetExamInfo_List', temp)
          .then((response) => {
            const { data } = response;
            console.log(data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {

  },
};

export default examInfo;
